const API_URL = process.env.REACT_APP_API_URL || 'http://localhost:5000/api';

// Helper function to get auth token
const getToken = () => {
  return localStorage.getItem('token');
};

// Helper function to make authenticated requests
async function fetchAPI(endpoint, options = {}) {
  const token = getToken();
  
  const config = {
    ...options,
    headers: {
      'Content-Type': 'application/json',
      ...options.headers,
    },
  };

  if (token) {
    config.headers['Authorization'] = `Bearer ${token}`;
  }

  const response = await fetch(`${API_URL}${endpoint}`, config);
  const data = await response.json();

  if (!response.ok) {
    throw new Error(data.error || 'Something went wrong');
  }

  return data;
}

// ============================================
// AUTH API
// ============================================

export const authAPI = {
  signup: async (name, email, password) => {
    const data = await fetchAPI('/auth/signup', {
      method: 'POST',
      body: JSON.stringify({ name, email, password }),
    });
    
    // Store token
    localStorage.setItem('token', data.token);
    localStorage.setItem('user', JSON.stringify(data.user));
    
    return data;
  },

  signin: async (email, password) => {
    const data = await fetchAPI('/auth/signin', {
      method: 'POST',
      body: JSON.stringify({ email, password }),
    });
    
    // Store token
    localStorage.setItem('token', data.token);
    localStorage.setItem('user', JSON.stringify(data.user));
    
    return data;
  },

  signout: () => {
    localStorage.removeItem('token');
    localStorage.removeItem('user');
  },

  getCurrentUser: async () => {
    return await fetchAPI('/auth/me');
  },

  isAuthenticated: () => {
    return !!getToken();
  },

  getStoredUser: () => {
    const userStr = localStorage.getItem('user');
    return userStr ? JSON.parse(userStr) : null;
  },
};

// ============================================
// SEARCHES API
// ============================================

export const searchesAPI = {
  getAll: async () => {
    return await fetchAPI('/searches');
  },

  create: async (searchData) => {
    return await fetchAPI('/searches', {
      method: 'POST',
      body: JSON.stringify(searchData),
    });
  },

  update: async (id, searchData) => {
    return await fetchAPI(`/searches/${id}`, {
      method: 'PUT',
      body: JSON.stringify(searchData),
    });
  },

  delete: async (id) => {
    return await fetchAPI(`/searches/${id}`, {
      method: 'DELETE',
    });
  },
};

// ============================================
// USER API
// ============================================

export const userAPI = {
  updateProfile: async (name, email) => {
    const data = await fetchAPI('/user/profile', {
      method: 'PUT',
      body: JSON.stringify({ name, email }),
    });
    
    // Update stored user
    localStorage.setItem('user', JSON.stringify(data));
    
    return data;
  },

  changePassword: async (currentPassword, newPassword) => {
    return await fetchAPI('/user/password', {
      method: 'PUT',
      body: JSON.stringify({ currentPassword, newPassword }),
    });
  },

  deleteAccount: async () => {
    const data = await fetchAPI('/user/account', {
      method: 'DELETE',
    });
    
    // Clear local storage
    authAPI.signout();
    
    return data;
  },
};



